<template>
  <el-form
    class="form"
    :inline="true"
    v-loading="loading"
    @submit.native.prevent="onSubmit"
    :model="form"
    label-width="150px"
  >
    <el-row :gutter="24">
      <el-col>
        <el-form-item label="是否开启支付宝收款">
          <el-select
            size="small"
            v-model="form.alipay_is_open"
            clearable
            placeholder="请选择"
          >
            <el-option value="Y" label="是" />
            <el-option value="N" label="否" />
          </el-select>
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item label="支付宝二维码">
          <el-upload
            :limit="1"
            :action="'/api/api/upload?ssid=' + ssid"
            :on-success="fileSuccess1"
            :file-list="file1"
            list-type="picture"
          >
            <el-button size="small" type="infor">点击上传</el-button>
          </el-upload>
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item label="支付宝支付说明">
          <el-input
            v-model="form.alipay_content"
            type="textarea"
            :cols="20"
            :rows="10"
            placeholder="请输入银行名称"
          ></el-input>
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item label="是否开启微信收款">
          <el-select
            size="small"
            v-model="form.wx_is_open"
            clearable
            placeholder="请选择"
          >
            <el-option value="Y" label="是" />
            <el-option value="N" label="否" />
          </el-select>
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item label="微信二维码">
          <el-upload
            :limit="1"
            :action="'/api/api/upload?ssid=' + ssid"
            :on-success="fileSuccess2"
            :file-list="file2"
            list-type="picture"
          >
            <el-button size="small" type="infor">点击上传</el-button>
          </el-upload>
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item label="支付宝支付说明">
          <el-input
            v-model="form.wx_content"
            type="textarea"
            :cols="20"
            :rows="10"
            placeholder="请输入银行名称"
          ></el-input>
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item label="银行名称">
          <el-input
            v-model="form.bank_name"
            placeholder="请输入银行名称"
          ></el-input>
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item label="银行开户姓名">
          <el-input
            v-model="form.bank_user"
            placeholder="请输入银行开户姓名"
          ></el-input>
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item label="银行卡号">
          <el-input
            v-model="form.bank_card"
            placeholder="请输入银行卡号"
          ></el-input>
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item label="银行充值返现利率">
          <el-input
            v-model="form.bank_back_apr"
            step="0.01"
            type="number"
            placeholder="请输入利率"
          >
            <template slot="append">%</template>
          </el-input>
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item label="开启银行充值返现">
          <el-select
            size="small"
            v-model="form.bank_is_back"
            clearable
            placeholder="请选择"
          >
            <el-option value="Y" label="是" />
            <el-option value="N" label="否" />
          </el-select>
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item label="是否开启银行转账">
          <el-select
            size="small"
            v-model="form.bank_is_open"
            clearable
            placeholder="请选择"
          >
            <el-option value="Y" label="是" />
            <el-option value="N" label="否" />
          </el-select>
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item label="最小充值金额">
          <el-input
            v-model="form.invest_min_money"
            placeholder="请输入最小充值金额"
          ></el-input>
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item label="最小提现金额">
          <el-input
            v-model="form.cost_min_money"
            placeholder="请输入最小提现金额"
          ></el-input>
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item label="最小互转金额">
          <el-input
            v-model="form.huzhan_min_money"
            placeholder="请输入最小互转金额"
          ></el-input>
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item>
          <el-button type="primary" native-type="submit">保存</el-button>
        </el-form-item>
      </el-col>
    </el-row>
  </el-form>
</template>
<script>
import Fetch from '../../utils/fetch'
import { Message } from 'element-ui'

export default {
  data() {
    return {
      config: {
        status: {},
      },
      form: {
        bank_name: '',
        wx_is_open: '',
        alipay_code: [],
        wx_code: '',
        alipay_is_open: '',
        bank_user: '',
        bank_card: '',
        bank_is_open: '',
        invest_min_money: 0,
        cost_min_money: 0,
        alipay_content: '',
        wx_content: '',
        huzhan_min_money: 0,
        bank_back_apr: 0,
        bank_is_back: 'N',
      },
      fileList: [],
      titleFileList: [],
      loading: false,
    }
  },
  computed: {
    ssid() {
      return localStorage.getItem('ssid')
    },
  },
  mounted() {
    this.loading = true
    Fetch('/sys/config/info', {
      type: 'pay',
    })
      .then(r => {
        this.loading = false
        for (const key in this.form) {
          if (this.form.hasOwnProperty(key)) {
            const item = r.data[key]
            this.form[key] = item
            this.file2 = r.data.wx_code
              ? [
                  {
                    name: r.data.wx_code.split('/').pop(),
                    url: r.data.wx_code,
                  },
                ]
              : []
            this.file1 = r.data.alipay_code
              ? [
                  {
                    name: r.data.alipay_code.split('/').pop(),
                    url: r.data.alipay_code,
                  },
                ]
              : []
          }
        }
      })
      .catch(() => (this.loading = false))
  },
  methods: {
    fileSuccess1(res, file) {
      this.file1 = [file]
      this.form.alipay_code = res.data.file
    },
    fileSuccess2(res, file) {
      this.file2 = [file]
      this.form.wx_code = res.data.file
    },
    handleImageAdded(file, Editor, cursorLocation, resetUploader) {
      const formData = new FormData()
      formData.append('file', file)
      Fetch('/api/api/upload', formData).then(result => {
        let url = result.data.file
        Editor.insertEmbed(cursorLocation, 'image', url)
        resetUploader()
      })
    },
    onSubmit() {
      Fetch('/sys/config/update', {
        type: 'pay',
        ...this.form,
      })
        .then(() => {
          this.loading = false
          Message.success('操作成功')
        })
        .catch(() => {
          Message.error('操作失败')
          this.loading = false
        })
    },
  },
}
</script>
<style lang="less" scoped>
.form {
  /deep/ .el-input__inner {
    width: 300px;
  }

  /deep/ .el-textarea {
    width: 300px;
  }
}
</style>
